html,body{
  margin: 0 auto;
  font-size: 1em;
}
.app{
  width:100%;
}
ul li {
  list-style: none;
}
.chatBox{
  width: 100%;
  height:95%;
  font-size: inherit;
}

.textBox{
  position: absolute;
  bottom: 0;
  min-height: 30px;
  width: calc(80% - 2px); 
  height: calc(5% - 5px);
  border-top:1px solid #000;
  font-size: inherit;
  padding: 4px;
}
.send{
  position: absolute;
  bottom: 0;
  left: 80%;
  min-height: 42px;
  height: calc(5% + 7px);
  width: 20%
}


